
* {
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
}

.container__blocks {
  display: flex;
  flex-wrap: wrap;
}

.container__block {
  display: flex;
  height: 300px;
  min-width: 33%;
  flex-grow: 1;
  background-color: lightblue;
  color: #fff;
  align-items: center;
  justify-content: center;
}

.block-1 {
  background-color: rgb(255, 0, 0);
}

.block-2 {
  background-color: rgb(204, 0, 0);
}

.block-3 {
  background-color: rgb(153, 0, 0);
}

.block-4 {
  background-color: rgb(102, 0, 0);
  order: 3;
}

.block-5 {
  background-color: rgb(51, 0, 0);
  order: 2;
}

.block-6 {
  background-color: rgb(0, 0, 0);
  order: 1;
}

@media (min-width:600px) and  (max-width: 899px) {

  .container__block {
    width: 50%;
  }

  .block-4 {
    order: 1;
  }
  .block-3 {
    order: 2;
  }
  .block-6 {
    order: 3;
  }
}

@media (max-width: 599px) {
  .container__block {
    width: 100%;
  }
  .block-4,
  .block-5,
  .block-6 {
    order: 0;
  }
}
